@use "./variables";

/*-----------------------------*\
    #Mixins
\*-----------------------------*/

@mixin flexify($al-con: center, $js-con: center, $flex-d: row) {
  display: flex;
  align-items: $al-con;
  justify-content: $js-con;
  flex-direction: $flex-d;
}

@mixin respond-to($breakpoint) {
  @media screen and (min-width: map-get($map:variables.$breakpoints,$key:$breakpoint)) {
    @content;
  }
}
